@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px 'Poppins', sans-serif;
    background: #F0F0F5;
    -webkit-font-smoothing: antialiased;
    padding-bottom: 20px;
}

input, button {
    font: 400 18px 'Poppins', sans-serif;
}

button {
    cursor: pointer;
}

.app {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
}
.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.container h1{
    margin-top: 10px;
    margin-bottom: 20px;
}
.images {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;
}

.languages h3{
    width: 100%;
    text-align: center;
}

.logos {
    width: 150px;
    height: 150px;
    padding: 10px;
    border-radius: 8px;
    background-color: #FFF;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.computer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.computer  img{
    width: 100%;
}

.computer div{
    position: absolute;
    top: 200px;
    left: 70px;
    background-color: transparent;
    display: flex;
    flex-direction: column;
}

.computer h1{
    color: #FFF;
    font-weight: bold;
    font-size: 25pt;
    margin-bottom: 30px;
}

.computer h3{
    color: #FFF;
    font-weight: bold;
    font-size: 15pt;
}
.presentation {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.presentation p{
    text-align: justify;
    font-size: 16pt;
}
header {
    display: flex;
    width: 100%;
    background-color: #000;

    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.profiles {
    display: flex;
    flex-direction: row;
    width: 100px;
    justify-content: space-between;
    align-items: center;
}

.profiles a {
    text-align: center;
    color: #FFF;
    cursor: pointer;
    font-size: 18pt;
}

.profiles a:hover {
    font-size: 20pt;
}

.welcome {
    display: flex;
    flex-direction: row;
    width: 200px;
    justify-content: space-between;
}

.welcome img {
    border-radius: 20px;
    height: 40px;
    width: 40px;
}

.welcome span{
    color: #FFF;
    font-size: 18pt;
    font-weight: bold;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
